
#user-page .header-avatar-wrap {
  padding: 30px 0;
  text-align: center;
  background-image: url("../image/avatar-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
#user-page .header-avatar-wrap .avatar-box {
    width: 70px;
    height: 70px;
    line-height: 70px;
    margin: auto;
    overflow: hidden;
    border-radius: 50%;
}
#user-page .header-avatar-wrap .user-name {
    margin-top: 5px;
}
#user-page .header-avatar-wrap .message-btn {
    position: fixed;
    top: 15px;
    right: 15px;
    font-size: 18px;
    color: #eceeef;
}
#user-page .header-avatar-wrap .message-btn i {
      margin-right: 0;
}
#user-page .my-functions .ico-box {
  font-size: 32px;
}
#user-page .my-servers .title-bar {
  border-bottom: 1px solid #eceeef;
}
#user-page .my-servers .grid-cell {
  margin-bottom: 20px;
}
#user-page .my-servers .ico-box {
  color: #fd5a37;
  font-size: 34px;
}
#user-page .more-server {
  color: #999999;
}
#user-page .subscribe-list .list-group-item {
  margin-bottom: 8px;
  background-color: #FFFFFF;
}
#user-page .swipe-list .mint-cell-wrapper {
  padding-top: 10px;
  padding-bottom: 8px;
  background-image: none;
  background-color: #FFFFFF;
  margin-bottom: 8px;
}
#user-page .swipe-list .mint-cell-label {
  margin-top: 10px;
}
#user-page .swipe-list .mint-cell:last-child {
  background-image: none;
}
#user-page .collect-list .time {
  position: absolute;
  right: 10px;
  bottom: 5px;
  font-size: 14px;
  color: #999999;
}
#user-page .collect-list .img {
  position: absolute;
  left: 10px;
  top: 10px;
  height: 45px;
  width: 60px;
}
#user-page .collect-list .mint-cell-title {
  min-height: 45px;
  margin-left: 75px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#user-page .collect-list .mint-cell-label {
  margin-top: 15px;
  font-size: 14px;
  color: #fd5a37;
}
#user-page .avatar-img {
  width: 25px;
  height: 25px;
  line-height: 25px;
  background-color: #ddd;
  color: #ffffff;
  text-align: center;
  border-radius: 50%;
}
#user-page .bind-list .mint-cell-title i.qq-ico {
  background-color: #5fa2fb;
}
#user-page .bind-list .mint-cell-title i.wx-ico {
  background-color: #3cc364;
}
#user-page .bind-list .mint-cell-title i.sj-ico {
  background-color: #e19967;
}
#user-page .bind-list .mint-cell-title [class^="icon-"], #user-page .bind-list .mint-cell-title [class*=" icon-"] {
  display: inline-block;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  font-size: 12px;
  color: #ffffff;
}
#user-page .bind-list .mint-cell-title [class^="icon-"] + .mint-cell-text, #user-page .bind-list .mint-cell-title [class*=" icon-"] + .mint-cell-text {
    margin-left: 25px;
}
#user-page .bind-list .mint-cell-value .mint-button {
  height: 22px;
  color: #fd5a37;
  border-color: #fd5a37;
  font-size: 13px;
}
#user-page .bind-list .mint-cell-value .mint-button.is-bind {
    border-color: #dddddd;
    color: #999999;
    pointer-events: none;
    cursor: not-allowed;
}
#user-page .username .mint-field-core::-webkit-input-placeholder {
  color: #fead9c;
}
#user-page .enterprise-go {
  text-align: center;
  color: #00b3ee;
  font-size: 16px;
}
#user-page .general-imgtxt2 .state {
  color: #fd5a37;
}



