
@charset "UTF-8";
#login-page {
  height: 100vh;
  padding-top: 65px;
  background-color: #ffffff;
  /* 获取验证码 和 密码可见按钮 */
}
#login-page .mint-cell-title {
    width: 4em;
}
#login-page .page-title {
    background-color: #ffffff;
    color: inherit;
    border-bottom: 1px solid #dddddd;
}
#login-page .login-box {
    margin-top: 25px;
    margin-bottom: 45px;
    margin-left: 15px;
    margin-right: 15px;
}
#login-page .login-other-info {
    margin-top: 8px;
    margin-bottom: 30px;
    color: #999999;
    font-size: 14px;
}
#login-page .register-goto {
    color: #fd5a37;
}
#login-page .get-check {
    font-size: 14px;
    height: 30px;
}
#login-page .show-pwd {
    margin-top: 8px;
}



