
#cate-page .grid-box .ico-box {
  position: relative;
}
#cate-page .grid-box .special-tag {
  position: absolute;
  top: 8px;
  right: -18px;
  width: 3em;
  line-height: 1.3;
  margin-left: 1.3em;
  margin-right: 1.3em;
  background-color: #f3f3f3;
  font-size: 12px;
  color: #ff0000;
  -webkit-transform: rotate(45deg) scale(1);
          transform: rotate(45deg) scale(1);
}
#cate-page .grid-box .special-tag:before, #cate-page .grid-box .special-tag:after {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
}
#cate-page .grid-box .special-tag:before {
    left: -1em;
    border-bottom: 1.3em solid #f3f3f3;
    border-right: 0 solid transparent;
    border-left: 1em solid transparent;
}
#cate-page .grid-box .special-tag:after {
    right: -1em;
    border-bottom: 1.3em solid #f3f3f3;
    border-right: 1em solid transparent;
    border-left: 0 solid transparent;
}



