
@charset "UTF-8";
#publish-form-page {
  padding-bottom: 60px;
}
#publish-form-page .topic-tips {
    padding: 10px 10px;
    color: #d5d5d5;
}
#publish-form-page .topic-tips .close-btn {
      font-size: 20px;
      margin-top: -3px;
      font-family: "宋体", serif;
}
#publish-form-page .upload-box {
    padding: 30px;
    text-align: center;
    background-color: #e3f3ff;
}
#publish-form-page .upload-box i {
      margin-right: 0;
}
#publish-form-page .upload-box .ico-box {
      display: inline-block;
      font-size: 32px;
      height: 2.1em;
      line-height: 2.1em;
      width: 2.1em;
      margin-bottom: 6px;
      border-radius: 50%;
      background-color: #79bef5;
      color: #ffffff;
}
#publish-form-page .upload-box .tips-text {
      color: #79bef5;
}
#publish-form-page .publish-acts {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
#publish-form-page .publish-acts .mint-button {
      float: left;
      width: 50%;
      border-radius: 0;
}
#publish-form-page .publish-acts:before, #publish-form-page .publish-acts:after {
      content: " ";
      display: table;
}
#publish-form-page .publish-acts:after {
      clear: both;
}



