
@charset "UTF-8";
#index-page {
  position: relative;
}
#index-page .top-search {
    padding: 10px;
    line-height: 30px;
    background-color: #fd5a37;
}
#index-page .top-search input::-webkit-input-placeholder {
      color: inherit;
}
#index-page .select-address, #index-page .top-search-box {
    color: #fff;
}
#index-page .select-address {
    margin-right: 8px;
}
#index-page .select-address i {
      margin-left: 5px;
}
#index-page .to-user-btn {
    margin-left: 10px;
    width: 2.1em;
    height: 2.1em;
    line-height: 2em;
    text-align: center;
    border-radius: 50%;
    background-color: #fff;
    color: #fd5a37;
}
#index-page .to-user-btn a {
      display: block;
}
#index-page .to-user-btn i {
      margin-right: 0;
}
#index-page .grid-view-box {
    padding-top: 10px;
}
#index-page .headline-box {
    line-height: 1.8em;
    padding: 10px 5px;
    padding-right: 15px;
}
#index-page .headline-title {
    width: 3em;
    margin-right: .5em;
    text-align: center;
    letter-spacing: .2em;
    font-style: oblique;
    font-size: 22px;
    color: #fd5a37;
    font-weight: bold;
    font-family: "Adobe 黑体 Std R";
}
#index-page .headline-more {
    display: inline-block;
    line-height: 3.5em;
    font-size: 16px;
    margin-left: 20px;
}
#index-page .headline-items {
    height: 3.6em;
}
#index-page .headline-item a {
    display: block;
    position: relative;
    padding-left: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#index-page .headline-item a:before {
      content: "";
      display: inline-block;
      width: 5px;
      height: 5px;
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -3px;
      border-radius: 50%;
      background-color: #ddd;
}
#index-page .hot-server .block-cont:before, #index-page .hot-server .block-cont:after {
    content: " ";
    display: table;
}
#index-page .hot-server .block-cont:after {
    clear: both;
}
#index-page .hot-server .media-base {
    float: left;
    width: 33.3%;
    padding: 0 10px;
    text-align: center;
    margin-bottom: 0;
}
#index-page .hot-server .media-title {
    font-size: inherit;
}
#index-page .hot-server .img {
    height: 70px;
    line-height: 70px;
}



