
body {
  background-color: #f3f3f3;
}
.mint-header,
.mint-navbar {
  border-bottom: 1px solid #eceeef;
}
.mint-header {
  height: 48px;
  background-color: #ffffff;
  color: inherit;
}
.mint-header .mint-header-title {
    font-size: 15px;
}
.mint-header.is-fixed + * {
    padding-top: 48px;
}
.mint-header .right-link {
    font-size: 14px;
}
.mint-search {
  height: auto;
}
.mint-searchbar {
  background-color: #f3f3f3;
  padding: 10px 15px;
}
.mint-searchbar-inner {
  height: 35px;
  background-color: #ffffff;
}
.mint-searchbar-inner .mintui-search,
.mint-searchbar-placeholder {
  font-size: 14px;
}
.mint-searchbar-cancel {
  color: inherit;
}
.mint-search-list {
  padding-top: 0;
  top: 55px;
  bottom: 0;
  overflow: auto;
  z-index: 2;
}
.mint-navbar .mint-tab-item.is-selected {
  color: #fd5a37;
  border-color: #fd5a37;
}
#footer .mint-tab-item-label {
  line-height: 45px;
  margin: -7px 0;
}
.mint-tab-item-label {
  font-size: 15px;
}
.mint-swipe-indicator.is-active {
  background-color: #fd5a37;
  opacity: 1;
}
.mint-cell i {
  margin-right: 0;
}
.mint-cell-text {
  vertical-align: inherit;
}
.mint-indexlist-nav {
  border-left-color: #eceeef;
}
.mint-header [class^="icon-"],
.mint-header [class*=" icon-"],
.mint-header .is-left,
.mint-header .is-right {
  font-size: 16px;
  color: #666666;
}
#header-search-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  padding-left: 38px;
  background-color: #f3f3f3;
}
.top-search-box {
  overflow: hidden;
  line-height: 30px;
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#80FFFFFF, endColorstr=#80FFFFFF)";
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#80FFFFFF, endColorstr=#80FFFFFF);
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  color: #666666;
}
.top-search-box i {
    margin-right: 8px;
}
.top-search-box .input-box {
    overflow: hidden;
}
.top-search-box input {
    display: block;
    width: 100%;
    height: 100%;
    border: none;
    background-color: transparent;
    outline: none;
}
.top-search-box input::-webkit-input-placeholder {
      color: inherit;
}
#top-back-btn {
  font-size: 16px;
  color: #666666;
}
#top-back-btn {
  position: fixed;
  left: 15px;
  top: 13px;
  height: 30px;
  line-height: 30px;
}
.bottom-bar-padding {
  padding-bottom: 45px;
}
#bottom-fixed-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  border-radius: 0;
  right: 0;
  background-color: #fd5a37;
}
.has-right-el .mint-cell-value {
  padding-right: 5em;
}
.block-wrap {
  background-color: #ffffff;
  margin-bottom: 8px;
}
.block-wrap .block-cont {
    padding: 5px 10px;
}
.title-bar {
  padding: 0 10px;
  margin-bottom: 5px;
}
.title-bar .title {
    font-size: 1.1em;
}
.general-imgtxt:before, .general-imgtxt:after,
.general-imgtxt2:before,
.general-imgtxt2:after {
  content: " ";
  display: table;
}
.general-imgtxt:after,
.general-imgtxt2:after {
  clear: both;
}
.general-imgtxt:last-child,
.general-imgtxt2:last-child {
  margin-bottom: 0;
}
.general-imgtxt .media-title,
.general-imgtxt2 .media-title {
  font-weight: normal;
  line-height: 1.2;
  height: 2.4em;
  margin-bottom: 5px;
  overflow: hidden;
  white-space: normal;
}
.general-imgtxt .img,
.general-imgtxt2 .img {
  height: 60px;
  line-height: 60px;
  width: 60px;
}
.general-imgtxt2 {
  position: relative;
  padding: 12px 12px 0px;
  margin-bottom: 8px;
}
.general-imgtxt2 .img {
    width: 80px;
    height: 80px;
    line-height: 80px;
    margin-bottom: 12px;
}
.general-imgtxt2 .media-title {
    line-height: 1.4;
    height: 2.8em;
    margin-bottom: 15px;
}
.general-imgtxt2 .bottom-acts {
    clear: both;
    padding: 10px 0;
    text-align: right;
    border-top: 1px solid #eceeef;
}
.general-imgtxt2 .mint-button {
    height: 30px;
    border-color: #fd5a37;
    color: #fd5a37;
    margin-left: 10px;
}
.general-imgtxt2 .like-btn.on i {
    color: #fd5a37;
}
.stamp-invalid {
  position: absolute;
  top: 22px;
  right: 22px;
  width: 3em;
  height: 3em;
  line-height: 3em;
  font-size: 20px;
  border-radius: 50%;
  color: #dcdcdc;
  -webkit-transform: rotate(-12deg);
          transform: rotate(-12deg);
}
.stamp-invalid:before, .stamp-invalid:after {
    content: "";
    position: absolute;
    border-radius: 50%;
}
.stamp-invalid:before {
    z-index: 9;
    left: 2px;
    right: 2px;
    top: 2px;
    bottom: 2px;
    border: 1px solid #dcdcdc;
}
.stamp-invalid:after {
    z-index: 8;
    left: -10px;
    right: -10px;
    top: -10px;
    bottom: -10px;
    border: 2px solid #dcdcdc;
}
.mint-cell-swipe-buttongroup {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mint-cell-swipe-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



